#pagination-wrapper {
  display: flex;
  gap: 20px;
}

.pagination-chang-link-box {
  display: flex;
  align-items: center;
  gap: 4px;

  cursor: pointer;
}

.pagination-change-link-text {
  color: rgba(0, 0, 0, 0.7);

  font-size: 14px;
  font-weight: 600;
  line-height: 140%;
}

.pagination-divider {
  color: rgba(0, 0, 0, 0.1);

  font-size: 14px;
  font-weight: 600;
  line-height: 140%;

  cursor: default;
}

.pagination-text-active {
  color: rgba(0, 0, 0, 1);

  font-size: 14px;
  font-weight: 600;
  line-height: 140%;

  cursor: default;
}

.pagination-text {
  color: rgba(0, 0, 0, 0.6);

  font-size: 14px;
  font-weight: 600;
  line-height: 140%;

  cursor: pointer;
}
